*{
  margin: 0;
  padding: 0;
}

/* INTRO */

header{
  width: 100%;
  height: 635px;
  background-image: url('./images/winnipeg.jpg');
  background-size: 100%;
  display: flex;
  flex-wrap: wrap;
}

header > .heading{
  color: whitesmoke;
  font-size: 4em;
  font-family: 'Righteous', cursive;
  width: 100%;
  margin: 200px 0 0 0;
  text-align: center;
}

header > .title{
  color: rgb(0, 0, 0);
  font-size: 3em;
  text-decoration-line: underline;
  font-family: 'Bungee Inline', cursive;
  width: 100%;
  text-align: center;
}

header > .contact {
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto 0 auto;
  text-align: center;
}

.contact > i{
  padding: 10px;
  height: 10px;
  font-size: 30px;
  cursor: pointer;
}

@media only screen and (max-width:1033px) and (min-width:451px) {
  header{
    height: 625px;
    background-size: cover; 
  }

  header > .heading{
    font-size: 3.5em;  
    margin: 160px 0 0 0;
  }
  
  header > .title{
    font-size: 2.5em;  
  }

  .contact > i{
    padding: 6px;
    height: 6px;
    font-size: 23px;
  }
}

@media only screen and (max-width:450px) and (min-width:0px) {
  header{
    height: 625px;
    background-size: cover; 
  }

  header > .heading{
    font-size: 3em;  
    margin: 160px 0 0 0;
  }
  
  header > .title{
    font-size: 2em;  
  }

  .contact > i{
    padding: 6px;
    height: 6px;
    font-size: 17px;
  }
}

/* SKILLS */

.skills{
  background-color: #ffffff; 
}

.skills > h1{
  margin: 150px 0 50px 0;
  font-family: 'Righteous', cursive;
  text-align: center;
  text-decoration-line: underline;
}

.skills-data{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.skills-data > div {
  width: 25%;
  padding: 2%;
}

.skills-data > div > i{
  font-size: 2em;
}
/*PORTFOLIO*/

.portfolio{
  background-color: #ffffff; 
}

.portfolio > h1{
  margin: 150px 0 50px 0;
  font-family: 'Righteous', cursive;
  text-align: center;
  text-decoration-line: underline;
}

.projects{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.projects > div{
  text-align: center;
  width: 50%;
  height: 400px;
  margin: 50px 0 0 0;
}

.projects > div{
  text-decoration-line: underline;
  cursor: pointer;
}

.projects > div > img{
  width: 80%;
  height: 350px;
  margin: 20px 0 0 0;
}